import { useEffect, useState } from 'react';
import './App.css';
import GraphEditor from './components/render_graph'
import MatrixDisplay from './components/matrix_display';
import { Matrix, EigenvalueDecomposition } from 'ml-matrix';

function convertToMatrix(matrix){
  if(matrix.data === undefined) return []
  return matrix.data.map((e)=>Array.from(e))
}
function isEmpty(matrix){
  return matrix === -1 || matrix.rows === 0 || matrix.columns === 0;
}
function toDiagonalMatrix(eigenvalues) {
  let matrix = [];
  for (let i = 0; i < eigenvalues.length; i++) {
      let row = new Array(eigenvalues.length).fill(0); 
      row[i] = eigenvalues[i]; 
      matrix.push(row);
  }
  return matrix;
}
function turnDiagonal(vector) {
  const n = vector.rows; // Since it's an n×1 column vector
  let diagonalMatrix = Matrix.zeros(n, n); // Create an n×n zero matrix

  for (let i = 0; i < n; i++) {
    diagonalMatrix.set(i, i, vector.get(i, 0)); // Set diagonal elements
  }

  return diagonalMatrix;
}


function App() {
  const [adjMatrix,setAdjMatrix] = useState([])
  const [laplaceMatrix,setLaplaceMatrix] = useState(-1)
  const [eigenvalues, setEigenvalues] = useState([])
  const [eigenvectors,setEigenvectors] = useState([])

  useEffect(()=>{
    setLaplaceMatrix(Matrix.eye(1,1))
  },[])
  useEffect(()=>{
    const n = adjMatrix.length
    var B=(new Matrix(adjMatrix))
    // nx1 vector 1
    const onevector = Matrix.ones(n,1)
    // multiple by B to get degree
    const degreeVector = B.mmul(onevector)
    B.sub(turnDiagonal(degreeVector))
    
    setLaplaceMatrix(B.mul(-1))
  },[adjMatrix])
  useEffect(()=>{
    if(!isEmpty(laplaceMatrix)){
      var e = new EigenvalueDecomposition(laplaceMatrix);
      setEigenvectors(e.eigenvectorMatrix)
      //it is always real by specturm theorem
      setEigenvalues(e.realEigenvalues)
    }
  },[laplaceMatrix])
  return (
    <div className="site">
      <div className="app-site">
        <div>
          <GraphEditor setAdjMatrix={setAdjMatrix} />
        </div>
        <div>
          <MatrixDisplay matrix={adjMatrix} name={"adj matrix"}/>
          <MatrixDisplay matrix={convertToMatrix(laplaceMatrix)} name={"laplacian matrix"}/>
          <MatrixDisplay matrix={toDiagonalMatrix(eigenvalues)} name={"eigenvalue"}/>
          <MatrixDisplay matrix={convertToMatrix(eigenvectors)} name={"eigenvectors matrix"}/>
        </div>
      </div>
    </div>
  );
}

export default App;
