import React from "react";

const boxStyle = {
    border: "1px black solid",
    display: "flex",
    padding: "3px",
    justifyContent: "flex-end",
}
const textStyle ={
}

const MatrixDisplay = ({ matrix ,name}) => {
    const padding="3px"
    const gap = "3px"
    return (matrix === undefined || matrix.length === 0) ? <div></div>: (
        <div style={boxStyle}>
            <div style={textStyle}>{name}</div>
            <div style={{ display: "flex", flexDirection: "column", gap: gap }}>
                <div style={{ display: "flex", gap: "3px", alignItems: "center", fontWeight: "bold" }}>
                    <div style={{ width: "40px", textAlign: "center" }}> </div> {/* Empty corner space */}
                    {matrix[0].map((_, colIndex) => (
                        <div key={colIndex} style={{ padding: padding, minWidth: "40px", textAlign: "center" }}>
                            {colIndex}
                        </div>
                    ))}
                </div>

                {matrix.map((row, rowIndex) => (
                    <div key={rowIndex} style={{ display: "flex", gap: gap, alignItems: "center" }}>
                        <div style={{ fontWeight: "bold", padding: padding, minWidth: "40px", textAlign: "center" }}>
                            {rowIndex}
                        </div>

                        {
                            
                            row.map((cell, colIndex) => (
                                <div 
                                    key={colIndex} 
                                    style={{ 
                                        padding: padding, 
                                        border: "1px solid black", 
                                        minWidth: "40px", 
                                        textAlign: "center", 
                                        backgroundColor: (cell === 0) ? "grey":""
                                    }}
                                >
                                    {cell.toFixed(2)}
                                </div>
                            ))
                        }
                    </div>
                ))}
            </div>
        </div>
    );
};

export default MatrixDisplay;
