import React, { useState, useRef, useEffect } from "react";
import CytoscapeComponent from "react-cytoscapejs";

const GraphEditor = ({setAdjMatrix}) => {
    const [nodeId, setNodeId] = useState(0);
    const [edgeId, setEdgeId] = useState(0);
    const cy = useRef(null);
    const [selectedNodes, setSelectedNodes] = useState([]); 

    useEffect(() => {
        if (cy.current) {
            cy.current.on("tap", "node", (event) => {
                const nodeId = event.target.id();
                handleNodeSelection(nodeId);
            });
        }
    }, []);

    const handleNodeSelection = (nodeId) => {
        setSelectedNodes((prev) => {
            if (prev.includes(nodeId)) {
                return prev.filter((id) => id !== nodeId); // Deselect if already selected
            }
            const newSelection = [...prev, nodeId];
            return newSelection;
        });
    };

    const addNode = () => {
        if (cy.current) {
            cy.current.add({
                group: "nodes",
                data: { id: `n${nodeId}`, label: `V_${nodeId}` },
                position: { x: nodeId * 60 + 100, y: nodeId + 200 },
            });
            setNodeId(nodeId + 1);
            setAdjMatrix(exportAdjMatrix())
        }
    };

    const addEdge = (source, target) => {
        if(source === undefined){
            console.log("Error no source")
            return
        } 
        if( target === undefined) target = source

        if (cy.current) {
            const findEdge = cy.current.edges().flatMap( e => {
                const src = e.data("source"), tgt = e.data("target"), eid = e.data("id");
                if((src === source && tgt === target) || (src === target && tgt === source)){
                    return [{
                        id: eid,
                        source: src,
                        target: tgt,
                    }]
                }
                return []
            })
            
            const newEdge = (findEdge.length === 0)
            
            //console.log(findEdge)
            if(newEdge){
                cy.current.add({
                    group: "edges",
                    data: { id: `e${edgeId}`, source: source, target: target },
                });
            } else{
                //console.log(findEdge[0].id)
                cy.current.getElementById(findEdge[0].id).remove();
            }
            setSelectedNodes([])

            setAdjMatrix(exportAdjMatrix())
            setEdgeId(edgeId + 1);
        }
    };
    const exportAdjMatrix = () => {
        if (!cy.current) return;

        const nodes = cy.current.nodes().map((n) => n.id()); // Get all node IDs
        const edges = cy.current.edges().map((e) => ({
            source: e.data("source"),
            target: e.data("target"),
        }));

        const matrix = Array(nodes.length).fill(0).map(() => Array(nodes.length).fill(0));

        edges.forEach(({ source, target }) => {
            const i = nodes.indexOf(source);
            const j = nodes.indexOf(target);
            if (i !== -1 && j !== -1) {
                matrix[i][j] = 1; 
                matrix[j][i] = 1; 
            }
        });

        //console.table(matrix);
        return matrix
    };

    return (
        <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
            <h2>Graph Editor</h2>
            <div style={{ width: "600px", height: "400px", border: "1px solid black" }}>
                <CytoscapeComponent
                    elements={[]} 
                    style={{ width: "100%", height: "100%" }}
                    layout={{ name: "grid" }}
                    cy={(cyRef) => { cy.current = cyRef; }}  // Store Cytoscape reference
                />
            </div>
            <div>
                <button onClick={addNode}>Add Vertex</button>
                <button onClick={()=>{
                    addEdge(selectedNodes[0], selectedNodes[1]);
                }}>Toggle Edge {selectedNodes.join(", ")}</button>
            </div>
        </div>
    );
};

export default GraphEditor;
